// Generic dashboard position value that can be any valid JSON value
export type DashboardPositionValue =
  | string
  | number
  | boolean
  | null
  | Record<string, unknown>
  | Array<unknown>;

// Dashboard position JSON is completely flexible - any key can have any valid JSON value
export interface DashboardPositionJson {
  [key: string]: DashboardPositionValue;
}

export interface CreateDashboardInterface {
  id: number;
  title: string | null;
  slug: string | null;
  description: string | null;
  positionJson: DashboardPositionJson | null;
  isPublished: boolean;
  tenantIdentifier: string;
  charts: Array<number>;
  roles: Array<string>;
  supersetId: number | null;
}

export interface UpdateDashboardInterface {
  title: string | null;
  slug: string | null;
  description: string | null;
  positionJson: DashboardPositionJson | null;
  isPublished: boolean;
  roles: Array<string>;
  charts: Array<number>;
  supersetId: number | null;
}

export interface DashboardInterface extends CreateDashboardInterface {
  createdAt: Date;
  updatedAt: Date;
}

export interface DashboardExportInterface {
  exportData: DashboardInterface;
  exportType: string;
  exportedAt: string;
}
